import React, { Component } from 'react';
import { connect } from 'react-redux';
import {
  Row,
  Col,
  Form,
  Table,
  Typography,
  Divider,
  Input,
  Button,
  Icon,
  DatePicker,
} from 'antd';
import { Link } from 'react-router-dom';
import notification from '@isomorphic/shared/isomorphic/components/Notification';
import SuperFetch from '../helpers/superFetch';
import moment from 'moment';
import { sum } from 'd3-array';
import Highlighter from 'react-highlight-words';
import base64url from 'base64url';
class Dashboard extends Component {
  constructor(props) {
    super(props);
    this.state = {
      searchText: '',
      claims: [],
    };
  }

  componentDidMount() {
    //https://app.swaggerhub.com/apis/Aptrinsic/rest-api/04-27-2018
    SuperFetch.get('claim/claims', {
      query: `[{[sequelize.Op.and]:[{createdAt: {[sequelize.Op.lt]:Date.parse('${moment()}')}}, {createdAt: {[sequelize.Op.gt]:Date.parse('${moment()
        .subtract(30, 'days')
        .toDate()}')}}]}]`,
    })
      .then(response => {
        if (response.status === 200) {
          this.setState({ claims: response.data });
        } else {
          notification('error', 'Something wrong in retrieving data');
        }
      })
      .catch(e => console.log(e));
  }
  getColumnSearchProps = dataIndex => ({
    filterDropdown: ({
      setSelectedKeys,
      selectedKeys,
      confirm,
      clearFilters,
    }) => (
      <div style={{ padding: 8 }}>
        <Input
          ref={node => {
            this.searchInput = node;
          }}
          placeholder={`Search ${dataIndex}`}
          value={selectedKeys[0]}
          onChange={e =>
            setSelectedKeys(e.target.value ? [e.target.value] : [])
          }
          onPressEnter={() => this.handleSearch(selectedKeys, confirm)}
          style={{ width: 188, marginBottom: 8, display: 'block' }}
        />
        <Button
          type="primary"
          onClick={() => this.handleSearch(selectedKeys, confirm)}
          icon="search"
          size="small"
          style={{ width: 90, marginRight: 8 }}
        >
          Search
        </Button>
        <Button
          onClick={() => this.handleReset(clearFilters)}
          type="primary"
          size="small"
          style={{ width: 90 }}
        >
          Reset
        </Button>
      </div>
    ),
    filterIcon: filtered => (
      <Icon type="search" style={{ color: filtered ? '#1890ff' : undefined }} />
    ),
    onFilter: (value, record) => {
      console.log(record);
      record[dataIndex] = record[dataIndex] === null ? '' : record[dataIndex];
      return record[dataIndex]
        .toString()
        .toLowerCase()
        .includes(value.toLowerCase());
    },
    onFilterDropdownVisibleChange: visible => {
      if (visible) {
        setTimeout(() => this.searchInput.select());
      }
    },
    render: text => (
      <Highlighter
        highlightStyle={{ backgroundColor: '#ffc069', padding: 0 }}
        searchWords={[this.state.searchText]}
        autoEscape
        textToHighlight={text ? text.toString() : ''}
      />
    ),
  });

  handleSearch = (selectedKeys, confirm) => {
    console.log(selectedKeys);
    confirm();
    this.setState({ searchText: selectedKeys[0] });
  };

  handleReset = clearFilters => {
    clearFilters();
    this.setState({ searchText: '' });
  };

  handleSubmit = e => {
    e.preventDefault();
    SuperFetch.get('claim/claims', {
      query: `[{[sequelize.Op.and]:[{createdAt: {[sequelize.Op.lt]:Date.parse('${this.dateRange.picker.state.value[1]}')}}, {createdAt: {[sequelize.Op.gt]:Date.parse('${this.dateRange.picker.state.value[0]}')}}]}]`,
    })
      .then(response => {
        if (response.status === 200) {
          this.setState({ claims: response.data });
        } else {
          notification('error', 'Something wrong in retrieving data');
        }
      })
      .catch(e => console.log(e));
    //console.log(this.dateRange.picker.state.value)
  };

  render() {
    const rowBlockstyling = {
      marginTop: '5%',
      padding: '24px',
    };
    const columns = [
      {
        title: 'UCN',
        dataIndex: 'id',
        render: (text, record) => {
          return (
            <Link to={`/dashboard/claim/${base64url(record.id)}`}>{text}</Link>
          );
        },
        //...this.getColumnSearchProps('id'),
        sorter: (a, b) => {
          return a.id > b.id;
        },
      },
      {
        title: 'Policy Number',
        dataIndex: 'policyNumber',
        ellipsis: true,
        ...this.getColumnSearchProps('policyNumber'),
      },
      {
        title: 'GOP',
        dataIndex: 'gop',
        render: val => (val ? <Icon type="check" /> : null),
        filters: [
          { text: 'Yes', value: true },
          { text: 'No', value: false },
        ],
        onFilter: (value, record) => {
          return record.gop === value;
        },
      },
      {
        title: 'Patient First Name',
        dataIndex: 'patientFirstName',
        ...this.getColumnSearchProps('patientFirstName'),
      },
      {
        title: 'Patient Last Name',
        dataIndex: 'patientLastName',
        ...this.getColumnSearchProps('patientLastName'),
      },
      {
        title: 'VIP',
        dataIndex: 'policyVip',
        render: val => (val ? 'Yes' : null),
        filters: [
          { text: 'Yes', value: true },
          { text: 'No', value: false },
        ],
        onFilter: (value, record) => {
          return record.policyVip === value;
        },
      },
      {
        title: 'Total(USD)',
        dataIndex: 'total',
        render: (text, record) => {
          return sum(
            record.ClaimInfoVisits,
            visit =>
              sum(visit.BillingInfos, billing => billing.value) /
              visit.billingRate
          ).toFixed(2);
        },
        sorter: (a, b) => {
          const constA = sum(
            a.ClaimInfoVisits,
            visit =>
              sum(visit.BillingInfos, billing => billing.value) /
              visit.billingRate
          ).toFixed(2);
          const constB = sum(
            b.ClaimInfoVisits,
            visit =>
              sum(visit.BillingInfos, billing => billing.value) /
              visit.billingRate
          ).toFixed(2);
          return constA - constB;
        },
      },
      {
        title: 'Date',
        dataIndex: 'createdAt',
        render: test => moment(test).format('DD MMM YYYY'),
        sorter: (a, b) => {
          return moment(a.createdAt) - moment(b.createdAt);
        },
      },
      {
        title: 'Status',
        dataIndex: 'status',
        render: (val, record) => {
          switch (val) {
            case 'pr':
              return 'Pending-Registration';
            case 'pe':
              return 'Pending-Evaluation';
            case 'pd':
              return 'Pending-Decision';
            case 're-pd':
              return 'Pending-Reevaluation';
            case 'cd':
              return 'Complete-Declined';
            case 'cp':
              return 'Complete-Partial Payment';
            case 'ca':
              return 'Complete-Approved';
            case 'cc':
              return 'Complete-Payment on Consideration';
            case 're-pr':
              return 'Pending-Review';
            default:
              return '';
          }
        },
        filters: [
          { text: 'Pending-Registration', value: 'pr' },
          { text: 'Pending-Evaluation', value: 'pe' },
          { text: 'Pending-Decision', value: 'pd' },
          { text: 'Pending-Reevaluation', value: 're-pd' },
          { text: 'Complete-Declined', value: 'cd' },
          { text: 'Complete-Partial Payment', value: 'cp' },
          { text: 'Complete-Approved', value: 'ca' },
          { text: 'Complete-Payment on Consideration', value: 'cc' },
          { text: 'Pending-Review', value: 're-pr' },
        ],
        onFilter: (value, record) => {
          return record.status === value;
        },
      },
    ];
    return (
      <Row>
        <Col span={20} offset={2}>
          <Row style={rowBlockstyling}>
            <Form onSubmit={this.handleSubmit} layout="inline">
              <Form.Item>
                <DatePicker.RangePicker
                  defaultValue={[moment().subtract(30, 'days'), moment()]}
                  format={'DD MMM YYYY'}
                  ref={input => (this.dateRange = input)}
                />
              </Form.Item>
              <Form.Item>
                <Button type="primary" htmlType="submit">
                  Submit
                </Button>
              </Form.Item>
            </Form>
          </Row>
          <Row style={rowBlockstyling}>
            <Col>
              <Typography.Title>My Claims to Process</Typography.Title>
            </Col>
            <Divider />
          </Row>
          <Row style={rowBlockstyling}>
            <Table
              columns={columns}
              dataSource={this.state.claims}
              rowKey="id"
              size={'small'}
            />
          </Row>
        </Col>
      </Row>
    );
  }
}

const WrappedDashboardForm = Form.create()(Dashboard);

export default connect(state => {
  return state.Claim;
}, {})(WrappedDashboardForm);
